import React, { FunctionComponent } from "react";
import { RouteComponentProps, Router } from "@reach/router";
import Main from "./Main";
import POTG from "./gallery/POTG/src/POTG";
import Volunteer from "./gallery/TUS-Voluteer/src/pages/Volunteer";

type Props = { component: FunctionComponent } & RouteComponentProps;

export const Route: FunctionComponent<Props> = ({ component: Component }) => (
  <Component />
);

function App() {
  return (
    <Router basepath="/">
      <Route component={Main} path="/" />
      <Route component={POTG} path="/POTG" />
      <Route component={POTG} path="/POTG/:name" />
      <Route component={Volunteer} path="/TUS-Volunteer" />
    </Router>
  );
}

export default App;
